.sidenav {
    height: 100%/* 100% Full-height */;
    width: 5vw /* 0 width - change this with JavaScript */;
    position: fixed /* Stay in place */;
    top: 0/* Stay at the top */;
    left: 0;
    background-color: $ocre /* Black*/;
    z-index: 2;
    border-right: solid 1px black;
    overflow-x: hidden /* Disable horizontal scroll */;
    transition: 0.3s /* 0.5 second transition effect to slide in the sidenav */; }

.title-left {
    width: 5vw;
    position: fixed /* Stay in place */;
    top: 0/* Stay at the top */;
    right: 0;
    background-color: $ocre;
    border-left: solid 1px black;
    height: 100%;
    z-index: 2; }


.menu-links {
    margin-top: 16vh;
    margin-left: -15vw;
    transition: 0.3s /* 0.5 second transition effect to slide in the sidenav */; }


.socials {
    position: absolute;
    bottom: 5vh;
    left: -15vw;
    transition: 0.3s; }


.socials-open {
    left: 2vw; }


.menu-links-open {
    margin-left: 0; }

/* The navigation menu links */
.sidenav a {
    padding: 0.5vw 0.5vw 1vw 2.2vw;
    text-decoration: none;
    display: block;
    transition: 0.3s;
    font-family: "Pangram-Medium";
    letter-spacing: 0.30vw;
    font-size: 1.1vw;
    line-height: 0; }


.sidebar-forward-link {
    margin-left: 1.5vw; }


/* When you mouse over the navigation links, change their color */
.sidenav a:hover {
    color: black; }


.sidenav-open {
    width: 20vw; }


.hamburger {
    margin-top: 10vh !important;
    float: right;
    margin-right: 0.7vw !important; }


.active {
    text-decoration: underline !important; }


.header-img {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 3;
    width: 100vw; }


.page-title {
    writing-mode: vertical-rl;
    text-orientation: upright;
    letter-spacing: 5vh;
    font-family: "Pangram-ExtraBold";
    font-size: 1.78vw;
    margin: 0;
    float: right;
    padding-right: 1.3vw;
    position: absolute;
    top: 20vh;
    right: 0; }


.page-left {
    writing-mode: vertical-rl;
    text-orientation: upright;
    letter-spacing: 5vh;
    font-family: "Pangram-ExtraBold";
    font-size: 1.8vw;
    margin: 0;
    float: right;
    position: absolute;
    top: 12vh;
    right: 0;
    margin-right: 1.3vw; }


.page-left-smaller {
    letter-spacing: 3vh;
    font-size: 1.7vw; }


.home-hide {
    display: none !important; }


@media only screen and (max-width: 600px) {
    .page-title {
        letter-spacing: 3vh;
        font-size: 5vw;
        padding-right: 2vw;
        top: 15vh; }

    .sidenav {
        width: 11vw; }


    .sidenav-open {
        width: 60vw; }

    .hamburger {
        margin-top: 6.2vh !important;
        padding: 10px 5px !important; }


    .hamburger-inner, ::before, ::after {
        width: 20px !important; }


    .sidenav a {
        font-size: 4.8vw;
        line-height: 6vw;
        padding-left: 5.5vw;
        letter-spacing: 1vw; }


    .socials-open {
        left: 5.5vw; }

    .menu-links {
        margin-top: 12vh;
        margin-left: -51vw; }

    .menu-links-open {
        margin-left: 0; }

    .sidebar-forward-link {
        margin-left: 3.5vw; } }


